.hero .container {
  position: relative;
  height: 100%;

  @media screen and (min-width: 768px) {
    width: 768px;
    padding-left: 34px;
    padding-right: 34px;
  }

  @media screen and (min-width: 1280px) {
    width: 88%;
  }

  @media screen and (min-width: 1280px) {
    width: 1080px;
    padding-left: 15px;
    padding-right: 15px;
  }
}

.hero .wrapper {
  position: absolute;
  top: 15%;
  z-index: 1;

  @media screen and (min-width: 768px) {
    top: 0%;
  }
}
.hero {
  margin: 0 auto;
  height: 502px;
  min-width: 320px;
  background-color: var(--pink-bgd);
  overflow: hidden;
  transition: all 0.3s ease 0s;
  @media screen and (max-width: 767px) {
    padding-top: 137px;
  }

  @media screen and (min-width: 768px) {
    padding-top: 102px;
    height: 400px;
  }

  @media screen and (min-width: 1080px) {
    padding-top: 120px;
    height: 420px;
  }

  @media screen and (min-width: 1280px) {
    padding-top: 171px;
    height: 665px;
  }
}
.hero {
  &__title {
    font-family: 'Titan One', cursive;
    font-size: 26px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    max-width: 218px;
    color: var(--main-text-color);
    margin-bottom: 19px;
    z-index: 5;
    transition: all 0.2s ease 0s;

    &--accent {
      font-size: 48px;
      line-height: 0.96;
      color: var(--accent-color);
    }

    @media screen and (min-width: 768px) {
      font-size: 22px;
      line-height: 1.13;
      max-width: 162px;
      margin-bottom: 20px;

      &--accent {
        font-size: 34px;
        line-height: 0.91;
        color: var(--accent-color);
      }
    }
    @media screen and (min-width: 1280px) {
      font-size: 38px;
      line-height: 1.15;
      max-width: 277px;
      margin-bottom: 25px;

      &--accent {
        font-size: 58px;
        line-height: 0.9;
        color: var(--accent-color);
      }
    }
  }

  &__desc {
    transition: all 0.2s ease 0s;

    @media screen and (max-width: 767px) {
      display: none;
    }

    @media screen and (min-width: 768px) {
      font-family: DM Sans;
      font-size: 8px;
      line-height: 1.5;
      color: var(--main-text-color);
      max-width: 136px;
      margin-left: 154px;
    }

    @media screen and (min-width: 1280px) {
      font-size: 14px;
      line-height: 1.7;
      max-width: 203px;
      margin-left: 180px;
    }
  }

  &__desc-text {
    margin-bottom: 7px;
    transition: all 0.2s ease 0s;

    @media screen and (min-width: 1280px) {
      margin-bottom: 20px;
    }
  }

  &__desc-link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: var(--main-text-color);

    @media screen and (min-width: 1280px) {
      width: 26px;
      height: 26px;
    }

    &-icon {
      width: 6px;
      height: 6px;
      fill: var(--accent-color);
      border-radius: 50%;
      transition: all 0.2s ease 0s;

      @media screen and (min-width: 1280px) {
        width: 8px;
        height: 8px;
      }
    }

    @media screen and (max-width: 767px) {
      display: none;
    }
  }
}

.primary-button {
  &__list {
    display: flex;
    flex-direction: row;
    transition: all 0.2s ease 0s;

    @media screen and (min-width: 768px) {
      margin-bottom: 66px;
    }
    @media screen and (min-width: 1280px) {
      margin-bottom: 112px;
    }
  }
  &__item {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.2s ease 0s;

    &:not(:last-child) {
      margin-right: 8px;

      @media screen and (min-width: 1280px) {
        margin-right: 15px;
      }
    }
  }
}

.primary-button {
  &__link {
    list-style: none;
    margin: 0;
    padding: 0;
    font-family: 'DM Sans', sans-serif;
    font-size: 400;
    color: inherit;
  }
}

.bg-group {
  transition: all 0.2s ease 0s;

  @media screen and (max-width: 319px) {
    display: none;
  }

  position: absolute;
  bottom: 66px;
  left: 36px;
  margin: 0%;
  border-radius: 50%;
  background: #ffa5ba;
  background-size: cover;
  background-repeat: no-repeat;

  width: 346px;
  height: 344px;

  &__icecream {
    position: absolute;
    top: 0;
    left: 81px;
    width: 171px;
    height: 419px;
  }

  @media screen and (min-width: 768px) {
    width: 324px;
    height: 322px;
    bottom: 46px;
    left: 38%;

    &__icecream {
      top: 32px;
      left: 71px;
      width: 143px;
      height: 336px;
    }
  }

  @media screen and (min-width: 1280px) {
    width: 538px;
    height: 538px;
    bottom: 75px;
    left: 388px;

    &__icecream {
      top: 56px;
      left: 119px;
      width: 236px;
      height: 360px;
    }
  }
}

.bg-group-secondary {
  &__list {
    list-style: none;
    margin: 0;
    padding: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-end;

    @media screen and (max-width: 767px) {
      display: none;
    }

    @media screen and (min-width: 1279px) {
      position: absolute;
      transition: all 0.2s ease 0s;
      right: 34px;
      top: 0;
    }
  }
}
.bg-group-secondary {
  &__quantity {
    position: relative;
    z-index: 1;
    display: flex;
    font-family: 'Titan One', cursive;
    font-size: 28px;
    line-height: 32px;
    letter-spacing: 0.04em;
    color: var(--accent-color);

    &::after {
      position: absolute;
      z-index: -1;
      right: -15%;
      content: '';
      width: 16px;
      height: 16px;
      border-radius: 50%;
      background: var(--main-text-color);
      transform: matrix(-1, 0, 0, 1, 0, 0);
    }

    @media screen and (min-width: 1280px) {
      font-size: 46px;
      line-height: 53px;
      &::after {
        width: 30px;
        height: 30px;
      }
    }
  }

  &__weight {
    font-family: 'DM Sans', sans-serif;
    font-size: 8px;
    line-height: 10px;
    text-align: right;
    color: var(--main-text-color);

    @media screen and (min-width: 1280px) {
      font-size: 14px;
      line-height: 18px;
    }
  }
}

.hero__bg-milk {
  position: absolute;
  transition: all 0.2s ease 0s;

  @media screen and (max-width: 767px) {
    display: none;
  }

  @media screen and (min-width: 768px) {
    width: 124px;
    height: 126px;
    left: 34px;
    bottom: 28px;
  }

  @media screen and (min-width: 1280px) {
    width: 188px;
    height: 191px;
    left: -28px;
    // bottom: 45px;
  }
}

.hero__bg-tasty {
  position: absolute;
  bottom: 0;
  right: 48px;
  transition: all 0.2s ease 0s;

  @media screen and (max-width: 767px) {
    display: none;
  }

  @media screen and (min-width: 768px) {
    width: 168px;
    height: 133px;
  }

  @media screen and (min-width: 1280px) {
    width: 293px;
    height: 232px;
    right: 0;
  }
}

//   button

.primary-btn--dark,
.primary-btn--light {
  border-style: none;
  border-radius: 20px;

  font-family: 'DM Sans', sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 8px;
  line-height: 1.25;
  text-align: center;

  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1), fill 250ms cubic-bezier(0.4, 0, 0.2, 1),
    border-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    outline-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-color 250ms cubic-bezier(0.4, 0, 0.2, 1),
    background-image 250ms cubic-bezier(0.4, 0, 0.2, 1),
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1), opacity 250ms cubic-bezier(0.4, 0, 0.2, 1),
    transform 250ms cubic-bezier(0.4, 0, 0.2, 1);

  cursor: pointer;
}

.primary-btn--dark {
  width: 61px;
  height: 24px;
  color: var(--main-text-color);
  background-color: var(--accent-color);

  &:hover,
  &focus {
    background-color: #c7133e;
  }

  @media screen and (min-width: 768px) {
    width: 63px;
    height: 24px;

    font-size: 8px;
  }

  @media screen and (min-width: 1280px) {
    width: 102px;
    height: 40px;

    font-size: 14px;
    line-height: 1.28;
  }
}

.primary-btn--light {
  width: 83px;
  height: 24px;

  color: var(--accent-color);
  background-color: var(--main-text-color);

  &:hover,
  &focus {
    background-color: #e1e1e1;
  }

  @media screen and (min-width: 768px) {
    width: 83px;
    height: 24px;

    font-size: 8px;
  }

  @media screen and (min-width: 1280px) {
    width: 138px;
    height: 40px;

    font-size: 14px;
    line-height: 1.28;
  }
}
